import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LayoutComponent } from './layout.component';
import { HomeComponent } from './home/home.component';
import {ProductsComponent} from './product/products.component'
import {NewsComponent} from './news/news.component'
import {TestifyComponent} from './testify/testify.component'
import {PrestationsComponent} from './prestations/prestations.component'
import {AboutUsComponent} from './about-us/about-us.component'

import { LoggedInGuard } from '../loggedIn.guard';
import { UserHomeComponent } from './user_home/user_home.component';
import { ViewNewsComponent } from './news/news.view.component';

const routes: Routes = [
    {
        path: '', component: LayoutComponent,
        children: [
            {
                path: 'home',
                component: HomeComponent,
            },
            {
                path: 'products',
                component: ProductsComponent,
            },
            {
                path: 'news',
                component: NewsComponent,
            },
            {
                path: 'news/:id',
                component: ViewNewsComponent,
            },
            {
                path: 'prestations',
                component: PrestationsComponent,
            },
            {
                path: 'testify',
                component: TestifyComponent,
            },
            {
                path: 'about_us',
                component: AboutUsComponent,
            },
            {
                path: 'user-home',
                loadChildren: 'app/layout/user_home/user_home.module#UserHomeModule',
                canActivate: [LoggedInGuard]
            },
            {
                path: '**',
                redirectTo: 'home'
            },
        ]
    }
];

@NgModule({
    declarations: [
        HomeComponent,
    ],
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class LayoutRoutingModule { }



