import { TestifyService } from './testify.service';

@NgModule({
    imports: [
        CommonModule,
        RouterModule,
    ],
    declarations: [
        TestifyComponent
    ],
    providers: [
        TestifyService,
        /*  { provide: LocationStrategy, useClass: HashLocationStrategy } */
      ],
})
export class TestifyComponentModule { }



